
body{
  background-color: rgb(80, 80, 80);
}
.App{
  background-color: rgb(185, 184, 184);
  min-height:800px;
  width: 800px;
  margin: 40px auto;
  position: relative;
  bottom: 20px;
  border-radius: 30px;
}



#newform input{
  background-color: lightgray;
  border: 0; 
  color: #fff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
  position: relative;
  right: -180px;
}
#newform input::placeholder{
  color: black;

}
#newform input:focus{
  outline: none;
}
#newform button{
	background: linear-gradient(-45deg, rgba(87, 225, 181, 1) 0%, rgba(0, 63, 255, 1) 100%);
	box-shadow: 0 5px 20px rgba(0, 63, 255, .5);
  cursor: pointer;
  position: relative;
  right: -180px;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
	font-weight: 300;
	text-align: center;
	font-size: 25px;
  line-height: 1.2;
  color: white;
  border-radius: 10px;
  height: 50px;
  width: 100px;
  border: none;
  top: 4px;
}


#newform button:focus{
  outline: none;
}


