.title {
  height: 10vh;
  position: relative;
  top: 5px;
  right: 250px;
}



svg text {
  font-family: Lora;
  letter-spacing: 10px;
  stroke:rgba(87, 225, 181, 1);
  font-size: 150px;
  font-weight: 700;
  stroke-width: 3;
 
  animation: textAnimate 5s infinite alternate;
  
}

@keyframes textAnimate {
  0% {
    stroke-dasharray: 0 50%;
    stroke-dashoffset:  20%;
    fill:hsl(0, 0%, 100%)

  }
  
  100% {
    stroke-dasharray: 50% 0;
    stroke-dashoffstet: -20%;
    fill: hsla(189, 68%, 75%,0%)
  }
  
}



